<template>
    <div>
        <tab-bar @on-pdf="handlerDownPdf"
                 @on-set="handlerToggleSet"
                 ref="tabBar"
                 :set="set"
                 :cs="rangeList.list"
                 :img="img"
                 :rt="rtable"/>
        <div class="wrapper-padding" v-show="!setShow">
            <div class="main-body">
                <div class="left-chart">
                    <echarts :zcb="jjfazcbMoney" :zsy="ntzhbMoney" />
                </div>
                <div class="right-table" ref="rightTable">
                    <table class="table">
                        <thead>
                        <tr>
                            <th>分类</th>
                            <th>项目</th>
                            <th>数值</th>
                        </tr>
                        </thead>
                        <tbody>
                        <tr>
                            <td class="bold" rowspan="2">节拍成本</td>
                            <td>站点日产量增加（件）</td>
                            <td v-active><span>{{zdrclzjNumber}}</span></td>
                        </tr>
                        <tr>
                            <td>节拍提升效益（元/年）</td>
                            <td v-active><span>{{jptsxyMoney}}</span></td>
                        </tr>
                        <tr>
                            <td class="bold" rowspan="2">工件成本</td>
                            <td>每年因工件错误导致的出错次数</td>
                            <td v-active><span>{{mnygjcwdzdccNumber}}</span></td>
                        </tr>
                        <tr>
                            <td>停线成本节约（元/年）</td>
                            <td v-active><span>{{txcbjyMoney}}</span></td>
                        </tr>
                        <tr>
                            <td class="bold" rowspan="2">记录返修数据出错成本</td>
                            <td>每年因数据记录出错次数</td>
                            <td v-active><span>{{mnysjjlccNumber}}</span></td>
                        </tr>
                        <tr>
                            <td>返修成本节约（元/年）</td>
                            <td v-active><span>{{fxcbjyMoney}}</span></td>
                        </tr>
                        <tr>
                            <td class="bold" rowspan="2">人工投入</td>
                            <td>每年返修数据导入时间节省（小时）</td>
                            <td v-active><span>{{mnfxsjdrsjjsTime}}</span></td>
                        </tr>
                        <tr>
                            <td>人工成本节约（元/年）</td>
                            <td v-active><span>{{rgjycbMoney}}</span></td>
                        </tr>
                        <tr>
                            <td class="bold">SQS产品成本</td>
                            <td>SQS解决方案总成本（元）</td>
                            <td v-active><span>{{jjfazcbMoney}}</span></td>
                        </tr>
                        </tbody>
                    </table>
                    <ul class="bottom-list">
                        <li>年投资回报（元）{{ntzhbMoney}}</li>
                        <li class="yellow">投资回报周期（月）{{tzhbzqTime}}</li>
                        <li>平均投资回报（元/次）{{pjtzhbMoney}}</li>
                    </ul>
                </div>
            </div>
            <div class="operate-wrapper">
                <span class="label">{{rangeList.list[rangeList.active].label}}</span>
                <div class="range">
                    <input :min="rangeList.list[rangeList.active].min"
                           :max="rangeList.list[rangeList.active].max"
                           v-model="range"
                           v-range
                           type="range">
                </div>
                <div class="inp">
                    <input type="number" v-model="range" class="input">
                </div>
            </div>
            <!--{{rangeList.list}}-->
            <ul class="select-wrapper">
                <li :class="{active: rangeList.active === key}"
                    v-for="(item, key) in rangeList.list"
                    @click="handlerTab(item, key)"
                    :key="key">
                    <i class="iconfont" :class="item.icon"></i>
                    <div>{{ item.name }}</div>
                </li>
            </ul>
        </div>
        <div class="none-echart">
            <echarts ref="Charts" :zcb="jjfazcbMoney" :zsy="ntzhbMoney"/>
        </div>
        <set-form v-model="setShow" :set-value="set" @on-ok="handlerOk"/>
    </div>
</template>

<script>
    import TabBar from '../../components/tab-bar'
    import Echarts from '../../components/ec-canvas'
    import SetForm from '../../components/set-form'
    import downpdf from '../../mixin/downpdf'

    export default {
        name: "rework",
        data() {
            return {
                setShow: false,
                range: 30,
                rangeList: {
                    active: 'jpts',
                    list: {
                        jpts: {
                            value: 30,
                            name: '生产节拍提升',
                            min: 0,
                            max: 100,
                            icon: 'icon-tisheng',
                            label: '使用SQS解决方案后节拍提升（%）',
                        },
                        mtgzcwcs: {
                            value: 5,
                            name: '工件导致错误',
                            min: 0,
                            max: 30,
                            icon: 'icon-baocuo',
                            label: '每天因工件错误导致的出错次数',
                        },
                        txsjsd: {
                            value: 40,
                            name: '停线时间缩短',
                            min: 0,
                            max: 100,
                            icon: 'icon-time',
                            label: '使用SQS解决方案后停线时间缩短（%）',
                        },
                        mtsjjlcwcs: {
                            value: 3,
                            name: '记录导致错误',
                            min: 0,
                            max: 30,
                            icon: 'icon-MESxitongjiemiantubiaoXpx-',
                            label: '每天因数据记录出错次数',
                        },
                        cccsjs: {
                            value: 80,
                            name: '出错次数减少',
                            min: 0,
                            max: 100,
                            icon: 'icon-zongcishu',
                            label: '使用SQS解决方案后出错次数减少（%）',
                        },
                        sjdrsjjy: {
                            value: 40,
                            name: '数据导入时间',
                            min: 0,
                            max: 100,
                            icon: 'icon-shijian',
                            label: '每天返修数据导入时间节省（分钟）',
                        },
                    }
                },
                set: {
                    szjbsj: {
                        name: '生产基本数据',
                        type: 'section-title'
                    },
                    bzdjpTime: {
                        name: '本站点节拍（分钟）',
                        value: 4,
                        type: 'input'
                    },
                    bzddjccjzMoney: {
                        name: '本站点单件产出价值（元）',
                        value: 50,
                        type: 'input'
                    },
                    ngzrNumber: {
                        name: '年工作日',
                        value: 200,
                        type: 'input'
                    },
                    ngfhgzr: {
                        name: '年高负荷工作日',
                        value: 100,
                        type: 'input'
                    },
                    rgsTime: {
                        name: '日工时（小时）',
                        value: 8,
                        type: 'input'
                    },
                    dcfxcbMoney: {
                        name: '单次返修成本（元）',
                        value: 30,
                        type: 'input'
                    },
                    scxmfzcbMoney: {
                        name: '生产线每分钟成本（元）',
                        value: 700,
                        type: 'input'
                    },
                    cxyggsfMoney: {
                        name: '产线员工工时费（元/小时）',
                        value: 100,
                        type: 'input'
                    },
                    cpcb: {
                        name: 'SQS产品成本',
                        type: 'section-title'
                    },
                    jjfaMoney: {
                        name: 'SQS产品解决方案（元）',
                        value: 150000,
                        type: 'input'
                    },
                    aztsMoney: {
                        name: '安装及调试成本（元）',
                        value: 20000,
                        type: 'input'
                    },
                }
            }
        },
        mixins: [downpdf],
        watch: {
            range: {
                handler(val) {
                    this.rangeList.list[this.rangeList.active].value = val
                },
                immediate: true
            }
        },
        computed: {
            // 节拍提升
            jpts() {
                return Number(this.rangeList.list.jpts.value) / 100
            },
            // 每天工作错误次数
            mtgzcwcs() {
                return Number(this.rangeList.list.mtgzcwcs.value)
            },
            // 停线时间缩短
            txsjsd() {
                return Number(this.rangeList.list.txsjsd.value) / 100
            },
            // 每天数据记录错误次数
            mtsjjlcwcs() {
                return Number(this.rangeList.list.mtsjjlcwcs.value)
            },
            // 出错次数减少
            cccsjs() {
                return Number(this.rangeList.list.cccsjs.value) / 100
            },
            // 数据导入时间节约
            sjdrsjjy() {
                return Number(this.rangeList.list.sjdrsjjy.value)
            },

            // 站点日产量增加（件）
            zdrclzjNumber() {
                const a = Number(this.jpts)
                const b = Number(this.set.rgsTime.value)
                const c = Number(this.set.bzdjpTime.value)
                return (a * b * 60 / c).toFixed(0)
            },
            // 节拍提升效益（元/年）
            jptsxyMoney() {
                const a = Number(this.zdrclzjNumber)
                const b = Number(this.set.bzddjccjzMoney.value)
                const c = Number(this.set.ngfhgzr.value)
                return (a * b * c).toFixed(0)
            },
            // 每年因工件错误导致的出错次数
            mnygjcwdzdccNumber() {
                const a = Number(this.mtgzcwcs)
                const b = Number(this.set.ngzrNumber.value)
                return (a * b).toFixed(0)
            },
            // 停线成本节约（元/年）
            txcbjyMoney() {
                const a = Number(this.txsjsd)
                const b = Number(this.mnygjcwdzdccNumber)
                const c = Number(this.set.dcfxcbMoney.value)
                return (a * b * c).toFixed(0)
            },
            // 每年因数据记录出错次数
            mnysjjlccNumber() {
                const a = Number(this.mtsjjlcwcs)
                const b = Number(this.set.ngzrNumber.value)
                return (a * b).toFixed(0)
            },
            // 返修成本节约（元/年）
            fxcbjyMoney() {
                const a = Number(this.mnysjjlccNumber)
                const b = Number(this.cccsjs)
                const c = Number(this.set.dcfxcbMoney.value)
                return (a * b * c).toFixed(0)
            },
            // 每年返修数据导入时间节省（小时）
            mnfxsjdrsjjsTime() {
                const a = Number(this.sjdrsjjy)
                const b = Number(this.set.ngzrNumber.value)
                return (a * b / 60).toFixed(0)
            },
            // 人工成本节约（元/年）
            rgjycbMoney() {
                const a = Number(this.mnfxsjdrsjjsTime)
                const b = Number(this.set.cxyggsfMoney.value)
                return (a * b)
            },
            // SQS解决方案总成本（元）
            jjfazcbMoney() {
                const a = Number(this.set.jjfaMoney.value)
                const b = Number(this.set.aztsMoney.value)
                return a + b
            },
            // 年投资回报（元）
            ntzhbMoney() {
                const a = Number(this.jptsxyMoney)
                const b = Number(this.txcbjyMoney)
                const c = Number(this.fxcbjyMoney)
                const d = Number(this.rgjycbMoney)
                return a + b + c + d
            },
            // 投资回报周期（月）
            tzhbzqTime() {
                const a = Number(this.jjfazcbMoney)
                const b = Number(this.ntzhbMoney)
                return (a / (b / 12)).toFixed(2)
            },
            // 平均投资回报（元/次）
            pjtzhbMoney() {
                const N28 = Number(this.ntzhbMoney)
                const N14 = Number(this.set.ngfhgzr.value)
                const N15 = Number(this.set.rgsTime.value)
                const N11 = Number(this.set.bzdjpTime.value)
                const G11 = Number(this.jpts)
                const N13 = Number(this.set.ngzrNumber.value)
                return (N28 / ((N14 * N15 * 60 / N11) * (1 + G11) + (N13 - N14) * N15 * 60 / N11)).toFixed(2)
            },
        },
        components: {TabBar, Echarts, SetForm},
        created() {
        },
        methods: {
            handlerToggleSet() {
                this.setShow = !this.setShow
            },
            handlerTab(item, key) {
                this.rangeList.active = key
                this.range = item.value
            },
            handlerOk(data) {
                this.set = data
            }
        },
    }
</script>

<style lang="scss">
    .chart-wrap {
        background-color: #f7f8fb;

        .right-table-wrap {

        }
    }
</style>
